<template>
  <div class="layout">
    <Affix>
      <Header :style="{width:'100%',background:'#ffffff'}">
        <div class="layout-logo">
          <img src="../../static/logo.png" alt="">
        </div>
        <div class="layout-ceiling-main">
          <Menu mode="horizontal" theme="light" active-name="home" width="auto" @on-select="goAnchor">
            <MenuItem name="home">
              网站首页
            </MenuItem>
            <MenuItem name="product">
              产品介绍
            </MenuItem>
            <MenuItem name="partner">
              招商加盟
            </MenuItem>
            <MenuItem name="about">
              联系我们
            </MenuItem>
          </Menu>
        </div>
      </Header>
    </Affix>
    <div :style="{minHeight:'960px'}">
      <!-- 使用动态的 transition name -->
      <router-view></router-view>

      <BackTop></BackTop>
      <Footer class="layout-footer-center" id="about">
        <div class="msg-header">
          <span>联系我们</span>
        </div>
        <div style="max-width: 1065px;margin: 50px auto;padding-top: 25px;">
          <Row type="flex" justify="center" align="center" style="text-align: center">
            <Col :xs="{span:24}" :sm="{span:11}" :md="{span:7}" :lg="{span:7}" style="padding: 5px;text-align: left">
            <h3 style="padding-bottom: 25px">杭州童享科技有限公司</h3>
            <p v-for="(obj,index) in describe" :key="index" style="line-height: 25px">{{obj}}</p>
            </Col>
            <Col :xs="{span:24}" :sm="{span:11}" :md="{span:7}" :lg="{span:7}" style="padding: 5px;">
            <p style="padding-bottom: 25px">招商微信号</p>
            <img src="../../static/code/codeA.png" alt="" style="object-fit:cover;background-size: 60% 60%">
            </Col>
            <Col :xs="{span:24}" :sm="{span:11}" :md="{span:7}" :lg="{span:7}" style="padding: 5px;">
            <p style="padding-bottom: 25px">关注小司机，了解更多信息</p>
            <img src="../../static/code/codeB.png" alt="" style="object-fit:cover;background-size: 60% 60%">
            </Col>
          </Row>
        </div>

        <div>2017-Present &copy; 童享科技</div>
      </Footer>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'main',
    data () {
      return {
        describe: [
          '地址：浙江省杭州市余杭区文一西路1288号海创科技中心3幢1501',
          '电话：0571-88664212',
          '邮箱：guyili@xiaosiji.club',
        ],
        transitionName: 'slide-left',
      }
    },

    created () {
      this.$router.push({
        name: 'home',
      })
    },
    computed: {},
    // watch $route 决定使用哪种过渡
    watch: {
      '$route' (to, from) {
        console.log(to);

        const toDepth = to.path.split('/').length
        const fromDepth = from.path.split('/').length
        this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      }
    },
    methods: {
      goAnchor (selector) {
        switch (selector){
          case 'partner':
            this.$router.push({
              name:selector,
            })
            break;
          default:
            this.$router.push({
              name:'home',
            })
            window.location.href='#'+selector;
            break;
        }

      }
    }
  }
</script>
<style lang="less">
  .ivu-affix {
    z-index: 9999 !important;
  }

  .ivu-layout-header {
    box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.2) !important;
  }

  .ivu-menu-horizontal.ivu-menu-light:after {
    width: auto !important;
  }

  .ivu-menu-horizontal {
    height: auto;
    line-height: inherit;
  }

  .layout {
    background: #f5f7f9;
    position: relative;
    overflow: hidden;
  }

  .layout-logo img {
    width: 100px;
    height: 30px;
    background-size: cover;
    position: relative;
    float: left;
    top: 15px;
    left: 20px;
  }

  .layout-ceiling-main {
    float: right;
    margin-right: 15px;
    color: #f6f6f6;
    a {
      color: #fff;
    }
  }

  .layout-footer-center {
    text-align: center;
  }

  .msg-header {
    font-weight: 500;
    margin: 30px 0 10px;
    position: relative;
    span {
      text-align: center;
      display: inline-block;
      padding: 0 5px;
      position: relative;
      background: #19be6b;
      color: #ffffff;
      margin-left: 30px;
      font-size: 14px;
    }
  }

  .msg-header:before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: #e9eaec;
    position: absolute;
    top: 10px;
    left: 0;
  }
</style>



