import Main from '@/views/main.vue'

// 404页面
export const page404 = {
  path: '/*',
  name: 'error-404',
  meta: {
    title: '404-页面不存在'
  },
  component: resolve => {
    require(['@/views/error-page/404.vue'], resolve)
  }
}

//
export const otherRouter = {
  path: '/',
  component: Main,
  children: [
    {
      path: 'home/index',
      name: 'home',
      meta: {
        title: '网站首页'
      },
      component: resolve => {
        require(['@/views/home/index.vue'], resolve);
      }
    },
    {
      path: 'product/index',
      name: 'product',
      meta: {
        title: '车辆管理-编辑'
      },
      component: resolve => {
        require(['@/views/product/index.vue'], resolve);
      }
    },
    {
      path: 'partner/index',
      name: 'partner',
      meta: {
        title: '网点管理-编辑'
      },
      component: resolve => {
        require(['@/views/partner/index.vue'], resolve);
      }
    },

    {
      path: 'about/index',
      name: 'about',
      meta: {
        title: '运维管理-编辑'
      },
      component: resolve => {
        require(['@/views/about/index.vue'], resolve);
      }
    },
  ]
}
